:root {
  --ui-color-brand: #353535;
  --ui-color-n-000: #fff;
  --ui-color-n-100: #ebebeb;
  --ui-color-n-300: #aeaeae;
  --ui-color-n-500: #353535;
  --ui-color-n-700: #282828;
  --ui-color-n-900: #1a1a1a;
  --ui-color-background-primary: var(--ui-color-n-000);
  --ui-color-border: var(--ui-color-n-100);
  --ui-color-form-input: var(--ui-color-n-100);
  --ui-color-typography-heading: var(--ui-color-n-500);
  --ui-color-typography-body: var(--ui-color-n-900);
  --ui-color-typography-note: var(--ui-color-n-300);
  --ui-color-typography-button: var(--ui-color-n-000);
  --ui-typography-typeface: "Inter", sans-serif;
  --ui-typography-h1: 1.9375rem;
  --ui-typography-h2: 1.5625rem;
  --ui-typography-h4: 1rem;
  --ui-typography-p: 1rem;
  --ui-typography-s: .8125rem;
  --ui-typography-h1-leading: 1.2;
  --ui-typography-h2-leading: 1.2;
  --ui-typography-h4-leading: 1.25;
  --ui-typography-p-leading: 1.5;
  --ui-typography-margin-heading: .75rem;
  --ui-typography-margin-body: 1.125rem;
  --ui-layout-container: 1.25rem;
  --ui-layout-grid: 3.625rem;
  --ui-layout-gutter: 1rem;
  --ui-gap-cta: .75rem;
  --ui-gap-content: 1.25rem;
  --ui-radius-button: 5rem;
  --ui-radius-input: 5rem;
  --ui-radius-card: .5rem;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  background-color: var(--ui-color-background-primary);
  color: var(--ui-color-typography-body);
  font-family: var(--ui-typography-typeface);
  font-feature-settings: "liga", "kern";
  font-size: var(--ui-typography-p);
  font-weight: 400;
  line-height: var(--ui-typography-p-leading);
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  margin: 0 auto;
}

@media (prefers-color-scheme: light) {
  body {
    color: invert(var(--ui-color-n-100));
    background: #fbffda;
  }

  h2, h4 {
    color: var(--ui-color-typography-heading);
  }
}

@media (prefers-color-scheme: dark) {
  body {
    color: var(--ui-color-n-100);
    background: #202b38;
  }

  h2, h4 {
    color: invert(var(--ui-color-typography-heading));
  }
}

h1 {
  background: -webkit-linear-gradient(45deg, #09009f, #00ff95 60%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  filter: invert();
}

a {
  color: var(--ui-color-brand);
  text-decoration: none;
}

h1, h2, h4, p {
  margin-top: 0;
}

h1, h2 {
  margin-bottom: var(--ui-typography-margin-heading);
}

h1 {
  font-size: var(--ui-typography-h1);
  line-height: var(--ui-typography-h1-leading);
}

h2 {
  font-size: var(--ui-typography-h2);
  line-height: var(--ui-typography-h2-leading);
}

h4 {
  font-size: var(--ui-typography-h4);
  line-height: var(--ui-typography-h4-leading);
}

p {
  margin-bottom: var(--ui-typography-margin-body);
}

p:last-child {
  margin-bottom: 0;
}

strong {
  font-weight: 700;
}

small {
  font-size: var(--ui-typography-s);
}

.ui-text-note {
  color: var(--ui-color-typography-note);
  line-height: 1;
}

img, svg {
  height: auto;
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.ui-layout-container {
  padding-left: var(--ui-layout-container);
  padding-right: var(--ui-layout-container);
}

.ui-layout-flex, .ui-layout-grid {
  justify-content: center;
  align-items: center;
}

.ui-layout-flex {
  display: flex;
}

.ui-layout-grid {
  display: grid;
}

.ui-component-cta {
  row-gap: var(--ui-gap-cta);
  flex-direction: column;
}

input, button {
  color: inherit;
  font-family: inherit;
  font-size: var(--ui-typography-p);
  text-rendering: inherit;
  text-transform: none;
  outline: none;
  margin: 0;
  line-height: 1;
}

form {
  width: 100%;
}

.ui-component-form {
  background-color: var(--ui-color-form-input);
  border-radius: var(--ui-radius-input);
  grid-template-columns: minmax(0, 1fr) auto;
  padding: .25rem;
}

::placeholder {
  color: var(--ui-color-typography-note);
}

.ui-component-input {
  background-color: var(--ui-color-form-input);
  border: .0625rem solid var(--ui-color-form-input);
  border-radius: var(--ui-radius-input);
}

.ui-component-input-medium {
  height: 2.5rem;
  padding: .625rem 1rem .75rem;
}

button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  display: block;
}

.ui-component-button {
  border: .0625rem solid var(--ui-color-brand);
  border-radius: var(--ui-radius-button);
  text-align: center;
  font-weight: 700;
  line-height: 1;
  display: block;
}

.ui-component-button-primary {
  background-color: var(--ui-color-brand);
  color: var(--ui-color-typography-button);
}

.ui-component-button-medium {
  width: -moz-fit-content;
  width: fit-content;
  padding: .625rem .875rem .75rem;
}

.ui-component-card {
  border: .0625rem solid var(--ui-color-border);
  border-radius: var(--ui-radius-card);
  text-align: center;
  width: 100%;
  padding: 2.25rem 1.5rem;
  overflow: hidden;
}

.ui-component-card--image {
  margin-bottom: 2.25rem;
}

.ui-component-card--title {
  margin-bottom: .5rem;
}

.ui-section-header {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

.ui-section-header__layout {
  justify-content: space-between;
}

.ui-section-hero {
  text-align: center;
  padding-top: 5rem;
  padding-bottom: 5em;
}

.ui-section-content {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

.ui-section-content__layout {
  row-gap: var(--ui-gap-content);
}

.ui-section-footer {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

.ui-section-footer__layout {
  column-gap: var(--ui-layout-gutter);
}

.ui-section-footer--copyright {
  margin-bottom: 0;
  margin-right: auto;
}

@media screen and (min-width: 48rem) {
  :root {
    --ui-typography-h1: 2.1875rem;
    --ui-typography-h2: 1.75rem;
    --ui-typography-h4: 1.125rem;
    --ui-typography-p: 1.125rem;
    --ui-typography-s: .875rem;
    --ui-typography-margin-body: 1.25rem;
    --ui-layout-container: 4.25rem;
    --ui-layout-gutter: 1.5rem;
    --ui-gap-content: 1.5rem;
  }

  .ui-layout-container, .ui-layout-column-center {
    margin-left: auto;
    margin-right: auto;
  }

  .ui-layout-grid-2 {
    column-gap: var(--ui-layout-gutter);
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }

  .ui-layout-grid-2 div:nth-of-type(2) {
    top: calc(50% + (var(--ui-layout-gutter) / 2));
    justify-self: start;
    position: relative;
  }

  .ui-layout-grid-2 div:nth-of-type(2n+1) {
    justify-self: end;
  }

  .ui-layout-column-4 {
    width: calc((var(--ui-layout-grid) * 4)  + (var(--ui-layout-gutter) * 3));
  }

  .ui-layout-column-6 {
    width: calc((var(--ui-layout-grid) * 6)  + (var(--ui-layout-gutter) * 5));
  }

  .ui-component-card {
    padding-left: 2.25rem;
    padding-right: 2.25rem;
  }

  .ui-section-header, .ui-section-content, .ui-section-footer {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}

@media screen and (min-width: 64rem) {
  :root {
    --ui-layout-container: 0;
  }

  a {
    transition: all .25s;
  }

  a:not(.ui-component-button):hover {
    color: var(--ui-color-typography-body);
  }

  .ui-layout-container {
    width: 60rem;
  }
}

@media screen and (min-width: 75rem) {
  :root {
    --ui-typography-h1: 2.75rem;
    --ui-typography-h2: 2.1875rem;
    --ui-typography-h4: 1.4375rem;
    --ui-typography-margin-heading: 1rem;
    --ui-typography-margin-body: 1.75rem;
    --ui-layout-grid: 4rem;
    --ui-layout-gutter: 2rem;
    --ui-gap-content: 2rem;
  }

  .ui-text-intro {
    font-size: var(--ui-typography-h4);
  }

  .ui-layout-container {
    width: 70rem;
  }

  .ui-section-header {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }

  .ui-section-hero {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }

  .ui-section-content, .ui-section-footer {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

